import React, { Fragment, useState, useRef } from 'react';
import Alert from '../../alert';
import AlertIcon from '../../alert-icon';
import AlertLabel from '../../alert-label';
import ButtonText from '../../button-text';
import ButtonGroup from '../../button-group';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Row from '../../row';
import ScreenReader from '../../screen-reader';
import Select from '../../select';
import SelectGroup from '../../select-group';
import SelectGroupDelimiter from '../../select-group-delimiter';
import Typography from '../../typography';

const SelectExample = () => {
  const [stateOne, setStateOne] = useState({
    touched: false,
    valid: false,
    validated: false,
    value: ''
  });

  const refOneValidation = useRef();

  const handleStateOne = e => {
    setStateOne({
      ...stateOne,
      touched: true,
      validated: false,
      value: e.target.value
    });
  };

  const handleStateOneSubmit = () => {
    const valid = stateOne.value && stateOne.value.length > 0;

    setStateOne({
      ...stateOne,
      touched: false,
      valid,
      validated: true
    });

    if (!valid) {
      refOneValidation.current.focus();
    }
  };

  const handleStateOneReset = () => {
    setStateOne({
      touched: false,
      valid: false,
      validated: false,
      value: ''
    });

    refOneValidation.current.focus();
  };

  const [stateTwo, setStateTwo] = useState({
    day: '',
    errorText: 'Error: Please provide a valid Date of Birth',
    month: '',
    showError: false,
    touched: false,
    valid: false,
    validDay: false,
    validMonth: false,
    validYear: false,
    validated: false,
    validatedDay: false,
    validatedMonth: false,
    validatedYear: false,
    year: ''
  });

  const refTwoValidationDay = useRef();
  const refTwoValidationMonth = useRef();
  const refTwoValidationYear = useRef();

  const handleStateTwoDay = () => {
    setStateTwo({
      ...stateTwo,
      validatedDay: true
    });

    handleStateTwo();
  };

  const handleStateTwoMonth = () => {
    setStateTwo({
      ...stateTwo,
      validatedMonth: true
    });

    handleStateTwo();
  };

  const handleStateTwoYear = () => {
    setStateTwo({
      ...stateTwo,
      validatedYear: true
    });

    handleStateTwo();
  };

  const handleStateTwo = () => {
    const day = String(refTwoValidationDay.current.value);
    const month = refTwoValidationMonth.current.value;
    const year = String(refTwoValidationYear.current.value);

    const validDay = day.length > 0;
    const validMonth = month.length > 0;
    const validYear = year.length > 0;

    setStateTwo({
      ...stateTwo,
      day,
      month,
      touched: true,
      validDay,
      validMonth,
      validYear,
      validated: false,
      year
    });
  };

  const handleStateTwoSubmit = () => {
    const test = stateTwo.validDay && stateTwo.validMonth && stateTwo.validYear;
    const test2 = !stateTwo.validDay || !stateTwo.validMonth || !stateTwo.validYear;

    let errorCommaIndex;
    let errorString = 'Error: Please provide a valid ';
    let errorCount = 0;
    let errorArray = [
      'month',
      'day',
      'year'
    ];

    errorArray.map((e, i) => { // eslint-disable-line
      let validField = stateTwo['valid' + e.substring(0,1).toUpperCase() + e.substring(1)];
      if (!validField) {
        errorString += e + ', ';
        errorCount += 1;
      }
    });

    errorCommaIndex = errorString.lastIndexOf(', ');
    errorString = errorString.substring(0, errorCommaIndex);

    errorCommaIndex = errorString.lastIndexOf(', ');

    if (errorCount === 2) {
      errorString = errorString.replace(', ', ' and ');
    } else if (errorCount === 3) {
      errorString = errorString.substring(0, errorCommaIndex + 1) + ' and ' + errorString.substring(errorCommaIndex + 1);
    }

    setStateTwo({
      ...stateTwo,
      errorText: errorString,
      showError: test2,
      touched: false,
      valid: test,
      validated: true,
      validatedDay: true,
      validatedMonth: true,
      validatedYear: true
    });

    // @TODO: focus on first invalid select
    if (!stateTwo.valid) {
      if (!stateTwo.validMonth) {
        refTwoValidationMonth.current.focus();
      } else if (!stateTwo.validDay) {
        refTwoValidationDay.current.focus();
      } else if (!stateTwo.validYear) {
        refTwoValidationYear.current.focus();
      }
    }
  };

  const handleStateTwoReset = () => {
    setStateTwo({
      day: '',
      errorText: 'Error: Please provide a valid Date of Birth',
      month: '',
      showError: false,
      touched: false,
      valid: false,
      validDay: false,
      validMonth: false,
      validYear: false,
      validated: false,
      validatedDay: false,
      validatedMonth: false,
      validatedYear: false,
      year: ''
    });

    refTwoValidationMonth.current.focus();
  };

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Select</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Alert position="relative" alertType="information">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon>
                    <Icon name="info" resolution="low" />
                  </AlertIcon>
                  <AlertLabel>
                    Select takes all native React props directly, (e.g. onChange, etc)
                  </AlertLabel>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            label="Card Type"
            defaultValue=""
          >
            <option value="" disabled hidden />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Select (ReadOnly)</Typography>
          <Select
            label="Select Card"
            readOnly
            value="Visa Signature Card"
          >
            <option value="" disabled hidden />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Select (Disabled)</Typography>
          <Select
            disabled
            label="Select Card"
            value="signature_card"
          >
            <option value="" disabled />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Select with preselection</Typography>
          <Select
            label="Card Type"
            defaultValue="signature_card"
          >
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Select with validation</Typography>
          <Typography variant="caption">*=required</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            errorText="Error: A card type is required"
            helpText={stateOne.valid ? "Successfully selected card type" : !stateOne.touched ? "Please select a card type" : ''}
            invalid={stateOne.validated && !stateOne.valid}
            label="Card Type *"
            onChange={handleStateOne}
            ref={refOneValidation}
            required
            showErrorText={!stateOne.valid}
            showHelpText={Boolean(!stateOne.validated || stateOne.valid)}
            value={stateOne.value}
          >
            <option value="" disabled hidden />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <ButtonGroup>
            <ButtonText
              colorScheme="secondary"
              onClick={handleStateOneReset}
            >
              Reset
            </ButtonText>
            <ButtonText
              onClick={handleStateOneSubmit}
            >
              Validate
            </ButtonText>
          </ButtonGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select Group with validity states</Typography>
          <Typography variant="caption">*=required</Typography>
          <SelectGroup
            errorText={stateTwo.showError ? stateTwo.errorText : ''}
            helpText={stateTwo.valid ? "Successfully selected date of birth" : !stateTwo.touched ? "Please fill out your Date of Birth info field" : ''}
            invalid={(!stateTwo.valid && stateTwo.validated) || !stateTwo.validMonth || !stateTwo.validDay || !stateTwo.validYear}
            legend="Date of Birth"
            showErrorText={stateTwo.showError}
            showHelpText={Boolean(!stateTwo.validated || stateTwo.valid)}
          >
            <ScreenReader id="select-group-invalid-month">
              { stateTwo.validated && !stateTwo.validMonth ? <Fragment>Error: Please select a valid month</Fragment> : null }
            </ScreenReader>
            <Select
              aria-describedby="select-group-invalid-month"
              invalid={!stateTwo.validMonth && stateTwo.validatedMonth}
              label="Month *"
              onChange={handleStateTwoMonth}
              ref={refTwoValidationMonth}
              required
              showErrorText={false}
              value={stateTwo.month}
            >
              <option value="" disabled hidden />
              {
                [
                  'January',
                  'February',
                  'March',
                  'April',
                  'May',
                  'June',
                  'July',
                  'August',
                  'September',
                  'October',
                  'November',
                  'December'
                ].map((e, i) => (
                  <option key={i} value={e.substring(0, 3).toLowerCase()}>{e}</option>
                ))
              }
            </Select>
            <SelectGroupDelimiter> / </SelectGroupDelimiter>
            <ScreenReader id="select-group-invalid-day">
              { stateTwo.validated && !stateTwo.validDay ? <Fragment>Error: Please select a valid day</Fragment> : null }
            </ScreenReader>
            <Select
              aria-describedby="select-group-invalid-day"
              invalid={!stateTwo.validDay && stateTwo.validatedDay}
              label="Day *"
              onChange={handleStateTwoDay}
              ref={refTwoValidationDay}
              required
              showErrorText={false}
              value={stateTwo.day}
            >
              <option value="" disabled hidden />
              {
                Array
                  .from(new Array(31), (x, i) => (i + 1))
                  .map((e, i) => (
                    <option key={i} value={e}>{e}</option>
                  ))
              }
            </Select>
            <SelectGroupDelimiter> / </SelectGroupDelimiter>
            <ScreenReader id="select-group-invalid-year">
              { stateTwo.validated && !stateTwo.validYear ? <Fragment>Error: Please select a valid year</Fragment> : null }
            </ScreenReader>
            <Select
              aria-describedby="select-group-invalid-year"
              invalid={!stateTwo.validYear && stateTwo.validatedYear}
              label="Year *"
              onChange={handleStateTwoYear}
              ref={refTwoValidationYear}
              required
              showErrorText={false}
              value={stateTwo.year}
            >
              <option value="" disabled hidden />
              {
                Array
                  .from(new Array((new Date()).getFullYear() - 1900), (x, i) => (i + 1900))
                  .map((e, i) => (
                    <option key={i} value={e}>{e}</option>
                  ))
              }
            </Select>
          </SelectGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <ButtonGroup>
            <ButtonText
              colorScheme="secondary"
              onClick={handleStateTwoReset}
            >
              Reset
            </ButtonText>
            <ButtonText
              onClick={handleStateTwoSubmit}
            >
              Validate
            </ButtonText>
          </ButtonGroup>
        </Col>
      </Row>
    </Grid>
  );
};

export default SelectExample;
